@import url(https://fonts.googleapis.com/css?family=Raleway:100,100i,200,200i,300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i);
/* project styles and thier css variables ( declared below )

var(--danger)          --> rgba(255,40,40,1)      or  #ff2828
var(--danger-65)       --> rgba(255,40,40,0.65)
var(--danger-35)       --> rgba(255,40,40,0.35)

var(--medium-blue)     --> rgba(71,141,255,1)    or   #478dff
var(--medium-blue-85)  --> rgba(71,141,255,0.85)

var(--dark-green)      --> rgba(0,140,73,1)      or   #008C49
var(--dark-green-85)   --> rgba(0,140,73,0.85)

var(--bright-green)    --> rgba(58,191,73,1)     or   #3ABF49
var(--bright-green-85) --> rgba(58,191,73,0.85)

var(--medium-green)    --> rgba(75,183,77,1)     or   #4BB74D
var(--medium-green-10) --> rgba(75,183,77,0.1)

var(--light-green)     --> rgba( 172,209,71,1)   or   #ACD147
var(--light-green-85)  --> rgba( 172,209,71,0.85)

var(--blackish)        --> rgba(30,48,27,1)      or   #1E301B
var(--black-30)        --> rgba(0,0,0,0.3)
var(--black-20)        --> rgba(0,0,0,0.2)
var(--black-10)        --> rgba(0,0,0,0.1)

var(--dark-gray)       --> rgba(96,96,96,1)      or   #606060
var(--text-gray)       --> rgba(112,112,112,1)   or   #707070
var(--light-gray)      --> rgba(221,221,221,1)   or   #ddd

var(--white)           --> rgba(255,255,255,1)   or   #fff
var(--white-85)        --> rgba(255,255,255,0.85)

var(--border)          --> 1px solid var(--light-gray)
var(--border-radius)   --> 5px
*/

:root {
  --danger:            #ff2828;
  --danger-65:         rgba(255, 40, 40, 0.65);
  --danger-35:         rgba(255, 40, 40, 0.35);
  --medium-blue:       #478dff;
  --medium-blue-85:    rgba(71,141,255,0.85);
  --dark-green:        #008C49;
  --dark-green-85:     rgba(0,140,73,0.85);
  --bright-green:      #3ABF49;
  --bright-green-85:   rgba(58,191,73,0.85);
  --medium-green:      #4bb74d;
  --medium-green-10:   rgba(75,183,77,0.1);
  --medium-green-85:   rgba(75,183,77,0.85);
  --light-green:       #acd147;
  --light-green-85:    rgba( 172,209,71,0.85);
  --blackish:          #1e301b;
  --dark-gray:         #606060;
  --text-gray:         #707070;
  --black-30:          rgba(0,0,0,0.3);
  --black-20:          rgba(0,0,0,0.2);
  --black-10:          rgba(0,0,0,0.1);
  --light-gray:        #ddd;
  --white:             rgba(255,255,255,1);
  --white-85:          rgba(255,255,255,0.85);

  --main-font:         'Futura', 'Roboto', 'Century Gothic', sans-serif;
  --border:            1px solid var(--light-gray);
  --border-radius:     5px;
  --box-shadow:        0.2rem 0.2rem 1.5rem;
  --black-box-shadow:  0.2rem 0.2rem 1.5rem rgba(0,0,0,0.5);
  --mini-box-shadow:   0.2rem 0.2rem 1rem rgba(0,0,0,0.5);
}

@font-face {
  font-family: 'Futura';
  font-style: normal;
  font-weight: 400;
  src:
    local('Futura'),
    url(/Generosity-Market.github.io/static/media/futura-book-font.f9f02ed0.ttf) format('ttf');
}

body, * {
  margin: 0;
  padding: 0;
  font-family: 'Futura', 'Roboto', 'Century Gothic', sans-serif;
  font-family: var(--main-font);
}

*:focus,
*:active {
    outline: none;
}

/* navbar and menu colors and thier css variables

var(--black-30)        --> rgba(0,0,0,0.3)
var(--black-20)        --> rgba(0,0,0,0.2)
var(--black-10)        --> rgba(0,0,0,0.1)

var(--dark-gray)       --> rgba(96,96,96,1)      or   #606060
var(--text-gray)       --> rgba(112,112,112,1)   or   #707070
var(--light-gray)      --> rgba(221,221,221,1)   or   #ddd

var(--white)           --> rgba(255,255,255,1)   or   #fff
var(--white-85)        --> rgba(255,255,255,0.85)
*/

.topnav {
  background-color: var(--black-30);
  display: flex;
  height: 57px;
  justify-content: space-between;
  position: fixed;
  width: 100vw;
  z-index: 1;
}

.topnav .links {
  flex-basis: 15%;
  height: 100%;
  padding-top: 1rem;
  text-align: center;
}

.topnav i {
  color: var(--white);
  font-size: 20px;
}

.topnav .menuLogo.links {
  flex-basis: 40%;
  padding-top: 0rem !important;
}

.topnav .menuLogo img {
  height: 100%;
}

.BaseLayout {
  padding-bottom: 3rem;
}

.BaseLayout nav {
  background-color: var(--white);
  height: 100vh;
  padding: 2rem 0 0 2rem;
  position: fixed;
  -webkit-transition: left 0.4s ease-in-out;
  transition: left 0.4s ease-in-out;
  width: 92.25vw;
  z-index: 5;
}

.navLinks {
  color: var(--blackish);
  font-size: 1.5rem;
  margin-bottom: 2.25rem;
  text-shadow: 5px 3px 15px rgba(0,0,0,0.3);
}

.navLinks i {
  margin-right: 2rem;
  width: 5%;
}

nav .menuLogo {

}

nav .menuLogo img {
  width: 50%;
  text-align: center;
}

nav .closeMenu {
  position: absolute;
  right: 1rem;
  top: 1rem;
}

.closeMenu i {
  font-size: 28px;
  text-shadow: 5px 3px 15px rgba(0,0,0,0.3);
}

.BottomMenu {
  background-color: var(--white);
  bottom: 0px;
  display: flex;
  left: 0px;
  position: fixed;
  width: 100%;
  z-index: 2;

  box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.3);
}

.BottomMenu .navLinks {
  text-shadow: none;
  align-items: center;
  display: flex;
  flex-direction: column;
  flex-basis: 25%;
  font-size: 0.8rem;
  justify-content: center;
  margin-bottom: 0;
  padding: 0.75rem 0;
}

.BottomMenu .navLinks i {
  font-size: 1.25rem;
  margin: 0 0 0.25rem 0;
  text-align: center;
  width: 100%;
}

@media screen and (min-width: 767px) {




}

.fade-enter {
    opacity: 0.01;
}

.fade-enter.fade-enter-active {
    opacity: 1;
    -webkit-transition: opacity 300ms ease-in;
    transition: opacity 300ms ease-in;
}

.fade-exit {
    opacity: 1;
}

.fade-exit.fade-exit-active {
    opacity: 0.01;
    -webkit-transition: opacity 300ms ease-in;
    transition: opacity 300ms ease-in;
}
